import * as THREE from 'three';
import gsap from 'gsap';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';

export default class Camera {
    constructor(_option) {
        this.time = _option.time;
        this.sizes = _option.sizes;
        this.renderer = _option.renderer;
        this.paths = _option.paths;
        this.debug = _option.debug;

        this.container = new THREE.Object3D();
        this.container.matrixAutoUpdate = false;

        if (this.debug) {
            this.debugFolder = this.debug.addFolder('camera');
            // this.debugFolder.open();
        }

        this.setInstance();
        this.setOrbitControls();
    }

    setInstance() {
        const { width, height } = this.sizes.viewport;
        this.instance = new THREE.PerspectiveCamera(75, width / height, 0.1, 100);
        this.instance.position.set(0, 5, 30);
        this.container.add(this.instance);

        this.sizes.on('resize', () => {
            const { width, height } = this.sizes.viewport;
            this.instance.aspect = width / height;
            this.instance.updateProjectionMatrix();
        });

        const geometryy = new THREE.SphereGeometry(0.5, 32, 32);
        const materiall = new THREE.MeshBasicMaterial({ color: 'red' });
        this.instancee = new THREE.Mesh(geometryy, materiall);
        const { x, y, z } = this.paths.paths[0];
        this.instancee.position.set(x, y, z);
        // this.container.add(this.instancee);
    }

    setOrbitControls() {
        this.event = {};
        this.event.y = 0;
        this.event.deltaY = 0;
        this.event.touchY = 0;
        this.event.moveSpeed = 0.003;

        // this.tween = gsap.to(this.instance.position, { x: this.paths.maxDistance, ease: 'power1' });
        // gsap.ticker.fps(60);
        // gsap.ticker.add((time, deltaTime, frame) => {
        //     console.log(time, deltaTime, frame);
        //     this.tween.progress(this.event.x / this.paths.maxDistance);
        // });

        this.renderer.domElement.addEventListener('wheel', (_event) => {
            this.event.deltaY = (_event.wheelDeltaY * -1) || _event.deltaY;
            // this.event.deltaY *= -1;
            this.event.deltaY *= this.event.moveSpeed / 10;

            this.scroll();
        }, false);

        this.renderer.domElement.addEventListener('touchstart', (_event) => {
            const touch = (_event.targetTouches) ? _event.targetTouches[0] : _event;
            this.event.touchY = touch.pageY;
        }, false);

        this.renderer.domElement.addEventListener('touchmove', (_event) => {
            const touch = (_event.targetTouches) ? _event.targetTouches[0] : _event;
            this.event.deltaY = (this.event.touchY - touch.pageY) * this.event.moveSpeed;
            // this.event.deltaY *= -1;
            this.event.touchY = touch.pageY;

            this.scroll();
        }, false);

        this.time.on('tick', () => {
            const [position, rotateY] = this.paths.setPosition(this.event.y / this.paths.maxDistance);
            const { x, y, z } = position;
            // this.instancee.position.set(x, y, z);
            this.instance.position.set(x, y, z);
            this.instance.rotation.y = rotateY;
        });

        // this.instance.lookAt(new THREE.Vector3());

        // this.time.on('tick', () => {
        //     this.instance.position.x += this.event.deltaX;
        //     this.event.x = this.instance.position.x;
        // });

        // this.orbitControls = new OrbitControls(this.instance, this.renderer.domElement);
        // this.orbitControls.enableDamping = true;

        // this.time.on('tick', () => this.orbitControls.update());
    }

    scroll() {
        if ((this.event.y + this.event.deltaY) < 0) {
            this.event.deltaY = 0 - this.event.y;
            this.event.y = 0;
        } else if ((this.event.y + this.event.deltaY) >= this.paths.maxDistance) {
            this.event.deltaY = this.paths.maxDistance - this.event.y;
            this.event.y = this.paths.maxDistance;
        } else {
            this.event.y += this.event.deltaY;
        }
    }
}
