import * as THREE from 'three';

export default class Paths {
    constructor(_option) {
        this.debug = _option.debug;

        this.container = new THREE.Object3D();
        this.container.matrixAutoUpdate = false;

        this.numDrawing = 10;
        this.pathWidth = 3;
        this.pathLength = 10;
        this.floorHeight = 3;
        this.maxDistance = 0;
        this.paths = [];

        if (this.debug) {
            this.debugFolder = this.debug.addFolder('paths');
            this.debugFolder.open();
        }

        this.setPath();
        // this.setDrawPath();
    }

    setPath() {
        this.maxDistance = this.pathLength * this.numDrawing;

        const pointA = new THREE.Vector3(-this.pathLength / 2, 0, -this.pathLength / 2);
        const pointB = new THREE.Vector3(+this.pathLength / 2, 0, -this.pathLength / 2);
        const pointC = new THREE.Vector3(+this.pathLength / 2, 0, +this.pathLength / 2);
        const pointD = new THREE.Vector3(-this.pathLength / 2, 0, +this.pathLength / 2);

        this.paths.push(pointA, pointB, pointC, pointD);
    }

    setDrawPath() {
        const material = new THREE.LineBasicMaterial({ color: 'white' });
        const geometry = new THREE.BufferGeometry().setFromPoints(this.paths);
        const lines = new THREE.Line(geometry, material);

        this.container.add(lines);
    }

    setPosition(_progress) {
        const drawing = _progress * this.numDrawing;

        const indexA = Math.floor(drawing) % this.paths.length;
        const indexB = Math.floor(drawing + 1) % this.paths.length;
        const indexC = Math.floor(drawing + 2) % this.paths.length;

        const pointA = this.paths[indexA].clone();
        const pointB = this.paths[indexB].clone();
        const pointC = this.paths[indexC].clone();

        const factor = 0.1;
        const alpha = drawing - Math.floor(drawing);
        const alphaClip = (1 - factor) * alpha + factor;
        const alphaPow = alpha ** 10;

        const position = pointA.lerp(pointB, alphaClip).lerp(pointC, alphaPow * factor);

        const rotateList = [1.5, 1, 0.5, 0];
        const rotateFactor = (1 - alphaPow) * rotateList[indexA] + alphaPow * rotateList[indexB];
        let rotateY = Math.PI * rotateFactor;

        if (indexA + 1 === rotateList.length) {
            rotateY += 2 * Math.PI * (1 - alphaPow);
        }

        return [position, rotateY];
    }
}
