import * as THREE from 'three';

import Materials from './Materials';
import Walls from './Walls';
import Torus from './Torus';
import Plane from './Plane';
import Fox from './Fox';

export default class World {
    constructor(_option) {
        this.resources = _option.resources;
        this.time = _option.time;
        this.sizes = _option.sizes;
        this.paths = _option.paths;
        this.camera = _option.camera;
        this.renderer = _option.renderer;
        this.config = _option.config;
        this.debug = _option.debug;

        this.container = new THREE.Object3D();
        this.container.matrixAutoUpdate = false;

        if (this.debug) {
            this.debugFolder = this.debug.addFolder('world');
            this.debugFolder.open();
        }

        this.container = new THREE.Object3D();
        this.container.matrixAutoUpdate = false;

        this.setStartingScreen();
    }

    setStartingScreen() {
        this.resources.on('progess', (percent) => console.log(`progress ${percent}/100`));
        this.resources.on('ready', () => this.start());
    }

    start() {
        this.materials = new Materials({ resources: this.resources });

        this.setWalls();
        // this.setTorus();
        this.setPlane();
        // this.setFox();
    }

    setWalls() {
        this.walls = new Walls({
            paths: this.paths,
            config: this.config,
            debug: this.debugFolder,
        });
        this.container.add(this.walls.container);
    }

    setTorus() {
        this.torus = new Torus({
            material: this.materials.items.matcap.gold,
            debug: this.debugFolder,
        });
        this.container.add(this.torus.container);
    }

    setPlane() {
        this.plane = new Plane({
            material: this.materials.items.shader.plane,
            paths: this.paths,
            walls: this.walls,
            time: this.time,
            debug: this.debugFolder,
        });
        this.container.add(this.plane.container);
    }

    setFox() {
        this.fox = new Fox({
            resources: this.resources,
            time: this.time,
            debug: this.debugFolder,
        });
        this.container.add(this.fox.container);
    }
}
