import * as THREE from 'three';
import color from '../../models/walls/Wood_Barrel_Top_001_basecolor.jpg';
import height from '../../models/walls/Wood_Barrel_Top_001_height.png';

export default class Walls {
    constructor(_option) {
        this.paths = _option.paths;
        this.config = _option.config;
        this.debug = _option.debug;

        this.container = new THREE.Object3D();
        this.container.matrixAutoUpdate = false;

        this.outerWalls = [];
        this.innerWalls = [];

        if (this.debug) {
            this.debugFolder = this.debug.addFolder('walls');
            this.debugFolder.open();
        }

        this.setWalls();
    }

    setWalls() {
        const textureLoader = new THREE.TextureLoader();
        const colorTexture = textureLoader.load(color);
        const heightTexture = textureLoader.load(height);

        colorTexture.repeat.x = 3; colorTexture.wrapS = THREE.RepeatWrapping;
        heightTexture.repeat.x = 3; heightTexture.wrapS = THREE.RepeatWrapping;

        const outerLength = this.paths.pathLength + this.paths.pathWidth;
        const innerLength = this.paths.pathLength - this.paths.pathWidth;

        const outerGeometry = new THREE.PlaneGeometry(outerLength, this.paths.floorHeight, 32);
        const outerMaterial = new THREE.MeshBasicMaterial();
        outerMaterial.map = colorTexture;
        outerMaterial.displacementMap = heightTexture;

        const innerGeometry = new THREE.PlaneGeometry(innerLength, this.paths.floorHeight, 32);
        const innerMaterial = new THREE.MeshBasicMaterial();
        innerMaterial.map = colorTexture;
        innerMaterial.displacementMap = heightTexture;

        const signX = [0, 1, 0, -1];
        const signZ = [-1, 0, 1, 0];

        for (let i = 0; i < 4; i += 1) {
            const outerMesh = new THREE.Mesh(outerGeometry, outerMaterial);
            const innerMesh = new THREE.Mesh(innerGeometry, innerMaterial);

            this.container.add(outerMesh, innerMesh);

            this.outerWalls.push(outerMesh);
            this.innerWalls.push(innerMesh);

            outerMesh.position.set(signX[i] * outerLength / 2, 0, signZ[i] * outerLength / 2);
            outerMesh.rotation.y = 2 * Math.PI * (-i / 4);
            innerMesh.position.set(signX[i] * innerLength / 2, 0, signZ[i] * innerLength / 2);
            innerMesh.rotation.y = 2 * Math.PI * (-(i + 2) / 4);
        }
    }
}
