import * as THREE from 'three';

import testVertexShader from '../../shaders/test/vertex.glsl';
import testFragmentShader from '../../shaders/test/fragment.glsl';

import img1 from '../../models/image/img1.jpg';
import img2 from '../../models/image/img2.jpg';
import img3 from '../../models/image/img3.jpg';
import img4 from '../../models/image/img4.jpg';

export default class Plane {
    constructor(_option) {
        this.material = _option.material;
        this.paths = _option.paths;
        this.time = _option.time;
        this.walls = _option.walls;
        this.debug = _option.debug;

        this.container = new THREE.Object3D();
        this.container.matrixAutoUpdate = false;

        if (this.debug) {
            this.debugFolder = this.debug.addFolder('plane');
            this.debugFolder.open();
        }

        const textureLoader = new THREE.TextureLoader();
        this.texture = textureLoader.load(img1);
        this.texture1 = textureLoader.load(img2);
        this.texture2 = textureLoader.load(img3);
        this.texture3 = textureLoader.load(img4);

        this.setPlane();
    }

    async setPlane() {
        await new Promise(resolve => setTimeout(resolve, 2000));

        const size = this.paths.pathWidth;
        const ratio = this.texture.image.width / this.texture.image.height;
        const amp = Math.sqrt(size / ratio);

        const geometry = new THREE.PlaneGeometry(amp * ratio, amp, 50, 50);
        // const geometry = new THREE.PlaneGeometry(size, size, 50, 50);

        const material = new THREE.ShaderMaterial({
            vertexShader: testVertexShader,
            fragmentShader: testFragmentShader,
            uniforms:
            {
                uTime: { value: 0 },
                uThickness: { value: 0.5 },

                uNoiseAmp2: { value: 0.3 },
                uNoiseFreq2: { value: 8.8 },
                uSpeed2: { value: 2.0 },
                uFreq2: { value: 20 },
                uStrength2: { value: 0.25 },
                uAngle2: { value: 0.4 },

                uNoiseAmp3: { value: 0.1 },
                uNoiseFreq3: { value: 10 },
                uSpeed3: { value: 3.5 },
                uFreq3: { value: 50 },
                uStrength3: { value: 0.1 },
                uAngle3: { value: 0.5 },

                uTexture: { value: this.texture },
                uTexture1: { value: this.texture1 },
                uTexture2: { value: this.texture2 },
                uTexture3: { value: this.texture3 },
                uPoint: { value: new THREE.Vector2() },
            },
        });

        this.walls.outerWalls.forEach((wall) => {
            // const mesh = new THREE.Mesh(geometry, this.material);
            const mesh = new THREE.Mesh(geometry, material);
            mesh.position.set(-this.paths.pathLength / 2, 0, 0);
            wall.add(mesh);
        });

        this.time.on('tick', () => {
            // this.material.uniforms.uTime.value = this.time.elapsed / 1000;
            material.uniforms.uTime.value = this.time.elapsed / 1000;
        });
    }
}
